import React from 'react';
import { cn as bem } from '@bem-react/classname';
import convertDate from '../../utils/convert-date';
import { Role } from '../../utils/const';
import { UserOuput } from '../../types/user-type';
import './style.scss';

interface IProfileCard {
  profile: UserOuput;
}

function ProfileCard({ profile }: IProfileCard): JSX.Element {
  const cn = bem('ProfileCard');

  const convertRole = (role: string): string => {
    if (role === Role.Admin) return '';
    if (role === Role.SuperAdmin) return ' ';
    return '';
  };

  return (
    <div className={cn()}>
      <h2 className={cn('title')}> </h2>
      <p className={cn('text')}>
        <span className={cn('text', { fontWeight: 'bold' })}>Id: </span>
        {profile.uuid}
      </p>
      <p className={cn('text')}>
        <span className={cn('text', { fontWeight: 'bold' })}>Email: </span>
        {profile.email}
      </p>
      <p className={cn('text')}>
        <span className={cn('text', { fontWeight: 'bold' })}>
          Email :{' '}
        </span>
        {profile.isActivated ? '' : ''}
      </p>
      <p className={cn('text')}>
        <span className={cn('text', { fontWeight: 'bold' })}>: </span>
        {convertRole(profile.role)}
      </p>
      <p className={cn('text')}>
        <span className={cn('text', { fontWeight: 'bold' })}>
           :{' '}
        </span>
        {convertDate(profile.createdAt)}
      </p>
    </div>
  );
}

export default React.memo(ProfileCard);
